.about {
    justify-content: center;
    align-items: center;
    display: flex;
    width: 80%;
    gap: 40px;
    /* z-index: 1; */
    flex-direction: column;
    margin: 100px 0px;
}

.about-section1 {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    gap: 30px;
    width: 80%;
    height: auto;
}

.about-writeUpWrapper {
    display: flex;
    gap: 15px;
    flex-direction: column;
    justify-content: flex-end;
    background-color: #292929;
    align-items: flex-end;
    border-left: 0px;
    width: 100%;
    height: 100%;


}

.about-writeUpWrapper .about-bioText {
    font-size: 54px;
    color: #898989;
    width: 100%;

    height: auto;
    font-size: 14px;
    border-radius: 25px;
    cursor: pointer;
    transition: all 350ms ease;
}
 
.about-writeUpWrapper .about-bioText:hover {
    transform: scale(1.05, 1.05);
    padding: 15px;

    background-color: #292929;
    box-shadow: 0px 0px 5px rgb(161, 255, 10);
    ;
    color: white;

}

.about-imageWrapper {
    display: flex;
    height: auto;
    width: auto;
    z-index: 10;
}

.about-section2 {
    display: flex;
    width: 100%;

    position: relative;
    justify-content: center;
}

.about-imageContainer {
    width: 200px;
    height: 300px;
    object-fit: contain;
}

@media (max-width: 768px) {
    .about {
        justify-content: center;
        align-items: center;
        display: flex;
        width: 80%;
        gap: 40px;
        z-index: 1;
        flex-direction: column;
    }

    .about-section1 {
        width: 100%;

        display: flex;

        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: auto;
    }


    .about-writeUpWrapper {
        display: flex;
        gap: 15px;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border-left: 0px;
        width: 100%;
    }

    .about-writeUpWrapper .about-bioText {
        color: #898989;
        width: 100%;

        height: auto;
        font-size: 16px;
        border-radius: 10px;
        font-style: italic;
    }

 
    .about-section2 {
        display: flex;
        gap: 30px;
        width: 100%;

     }

    .about-imageWrapper {
        display: flex;
        height: auto;
        width: 100%;
        justify-content: flex-start;
        align-items: center;
        margin: unset;

    }
}