.flip-card {
    width: 100%;
    height: 100%;
    transition: all 0.4s;
    font-size: 30px;
    font-weight: 900;
    position: relative;
    overflow: hidden;
    flex-direction: column; 
}

.flip-card:hover {
    cursor: pointer;
    transform: scale(1.2);
}

.front-face,
.back-face {
    position: absolute;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.4s ease-in-out;

    overflow: hidden;
}

.front-face {
    opacity: 1;
    z-index: 2;
}

.flip-card:hover .front-face {
    opacity: 0;
    z-index: 0;
}

.back-face {
    opacity: 0;
    font-size: 0;
    transform: rotate(90deg) scale(-1);
    z-index: 1;
}

.flip-card:hover .back-face {
    opacity: 1;
    font-size: 1.8rem;
    transform: rotate(0deg) scale(1);
    z-index: 3;
}

.flip-card img {

    height: 100%;
    width: 100%;
    object-fit: cover;
}